import React from 'react'
import { useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import { MintboxWOBg } from 'assets/SVG'
import cssVar from 'views/mobileView/styles'
const BottomTabs = (props) => {
  const history = useHistory()
  const location = useLocation();
  const [activeTabs, setActiveTabs] = useState('invest-mintboxes')

  useEffect(() => {
    if (location.pathname.includes("/Invest/MutualFunds")) {
      setActiveTabs('invest-MFs')
    } else if (location.pathname.includes("/Invest/ETFs")) {
      setActiveTabs('invest-ETFs')
    } else if (location.pathname.includes("/Invest")) {
      setActiveTabs('invest-mintboxes')
    } 
  })

  return (
    <div className='bottom-nav'>
      <div className='bn-tab' onClick={() => {history.replace('/Invest/MutualFunds')}}>
        <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/web-icons/piggy-bank.svg' width='24px' height='24px' className={activeTabs === 'invest-MFs' ? 'darkGreenFilter' : 'silverFilter'} />
        <div className={activeTabs==='invest-MFs'?'footerTextActive':'footerText'} style={{fontSize:"12px"}}>Mutual funds</div>
      </div>
      <div className='bn-tab' onClick={() => {history.replace('/Invest')}}>
        <MintboxWOBg style={{fill: activeTabs === 'invest-mintboxes' ? cssVar.getPropertyValue('--darkGreen') : cssVar.getPropertyValue('--silver') }} width='24px' height='24px'/>
        <div className={activeTabs==='invest-mintboxes'?'footerTextActive':'footerText'} style={{fontSize:"12px"}}>Smart mintboxes</div>
      </div>
      <div className='bn-tab' onClick={() => {history.replace('/Invest/ETFs')}}>
        <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/web-icons/candlestick.svg' width='24px' height='24px' className={activeTabs === 'invest-ETFs' ? 'darkGreenFilter' : 'silverFilter'} />
        <div className={activeTabs==='invest-ETFs'?'footerTextActive':'footerText'} style={{fontSize:"12px"}}>Low cost ETFs</div>
      </div>
    </div>
  )
}

export default BottomTabs